@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Poppins-400.woff2") format("woff2"), url("../fonts/Poppins-400.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Poppins-500.woff2") format("woff2"), url("../fonts/Poppins-500.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/Poppins-600.woff2") format("woff2"), url("../fonts/Poppins-600.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Poppins-700.woff2") format("woff2"), url("../fonts/Poppins-700.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.container {
  width: 1168px;
  margin: 0 auto;
}

body {
  margin: 0;
  font-family: "Poppins", "Arial", sans-serif;
}

.page__wrapper {
  background: url("../img/promo@2x.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}

@media (max-width: 1168px) {
  .container {
    width: auto;
    padding: 0 10px;
  }
}
.header {
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 400;
  color: white;
  padding: 55px 0;
}

.header__nav {
  display: flex;
  justify-content: space-between;
}

.header__link {
  text-decoration: none;
  color: white;
  align-self: center;
  padding-left: 33px;
  position: relative;
}
.header__link:hover {
  color: #D3B47F;
}
.header__link svg {
  position: absolute;
  top: 0;
  left: 0;
}

.header__toggle {
  display: none;
}

@media (max-width: 1168px) {
  .header {
    padding: 20px 0;
  }
  .header__nav {
    flex-direction: column;
    position: relative;
  }
  .header__img {
    width: 200px;
    height: auto;
  }
  .header__logo {
    padding-left: 50px;
    align-self: center;
  }
  .header__list {
    flex-direction: column;
  }
  .header__toggle {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 50px;
    height: 50px;
    padding: 0;
    border: none;
  }
  .header__link {
    margin-top: 40px;
    align-self: flex-start;
    padding-left: 80px;
  }
  .header__link svg {
    left: 40px;
  }
  .header__nav--closed .header__toggle::before {
    content: "";
    position: absolute;
    display: block;
    top: 20px;
    right: 16px;
    width: 16px;
    height: 1.5px;
    background-color: black;
    box-shadow: 0 5px 0 0 black, 0 10px 0 0 black;
  }
  .header__nav--opened .header__toggle::before {
    content: "";
    position: absolute;
    display: block;
    top: 24px;
    right: 24px;
    width: 12px;
    height: 12px;
    background-image: url("../img/cross.svg");
  }
  .header__nav--closed .header__list,
  .header__nav--closed .header__link {
    display: none;
  }
}
.site-list {
  list-style-type: none;
  display: flex;
  margin: 0;
  padding: 0;
  align-items: center;
}

.site-list__item:not(:last-child) {
  margin-right: 45px;
}

.site-list__link {
  text-decoration: none;
  color: white;
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
}
.site-list__link:hover {
  color: #D3B47F;
}

@media (max-width: 1168px) {
  .site-list {
    align-items: stretch;
  }
  .site-list__item {
    padding: 10px 0 10px 50px;
    border-bottom: 1px solid #E7E7E7;
  }
  .site-list__item:not(:last-child) {
    margin-right: 0;
  }
}
.promo {
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 500;
  color: white;
  padding: 100px 0 265px;
}

.promo__bg-img {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: auto;
}

.promo__hours {
  text-transform: uppercase;
  font-size: 24px;
  line-height: 32px;
  margin-bottom: 24px;
}

.promo__slogan {
  font-size: 64px;
  line-height: 88px;
  margin: 0 0 40px;
}

.promo__footer {
  display: flex;
  justify-content: space-between;
}

@media (max-width: 1168px) {
  .promo {
    padding: 50px 0;
  }
  .promo__hours {
    font-size: 16px;
    margin-bottom: 10px;
  }
  .promo__slogan {
    font-size: 34px;
    line-height: 44px;
    margin: 0 0 20px;
  }
  .promo__footer {
    flex-direction: column;
    gap: 20px;
    align-items: center;
  }
}
.button {
  display: block;
  padding: 38px 65px;
  background-color: #3A4574;
  border: none;
  color: white;
  font-size: 24px;
  line-height: 24px;
  font-family: "Poppins", "Arial", sans-serif;
  cursor: pointer;
}
.button:hover {
  opacity: 0.8;
}

@media (max-width: 1168px) {
  .button {
    font-size: 16px;
    line-height: 16px;
    padding: 15px 20px;
  }
}
.socials-list {
  list-style-type: none;
  display: flex;
  margin: 0;
  padding: 0;
  align-items: center;
}

.socials-list__item:not(:last-child) {
  margin-right: 25px;
}

.socials-list__link {
  text-decoration: none;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  border: 1px solid white;
}
.socials-list__link:hover {
  color: #D3B47F;
  border-color: #D3B47F;
}

.menu-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  width: 469px;
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 32px;
}

.menu-list__item {
  border-bottom: 1px solid #E7E7E7;
}

.menu-list__title {
  font-size: 24px;
  line-height: 32px;
  font-weight: 600;
  margin: 20px 0 10px;
}

.menu-list__text {
  margin: 0 0 20px;
  padding: 0;
}

.menu-list__dish--first::after {
  content: "";
  width: 20px;
  display: inline-block;
  height: 5px;
  margin: 0 5px 0 10px;
  border-bottom: 5px dotted #D3B47F;
}

@media (max-width: 1168px) {
  .menu-list {
    width: auto;
    text-align: center;
  }
}
.week-menu {
  padding: 80px 0 90px;
  position: relative;
}
.week-menu::after {
  content: "";
  position: absolute;
  width: 25%;
  height: 502px;
  border: 2px solid #D3B47F;
  top: 131px;
  right: 25%;
  z-index: -5;
}

.week-menu__wrapper {
  display: flex;
  flex-wrap: wrap;
}

.week-menu__list {
  margin-right: 30px;
}

.week-menu__img--down {
  margin-right: 22px;
  margin-top: 152px;
}

@media (max-width: 1168px) {
  .week-menu {
    padding: 30px 0;
  }
  .week-menu::after {
    content: none;
  }
  .week-menu__wrapper {
    justify-content: space-around;
  }
  .week-menu__list {
    width: 100%;
  }
  .week-menu__img {
    margin: 0;
    display: inline;
    width: 160px;
    height: auto;
  }
}
.quotes {
  background-color: #3A4574;
  color: white;
  padding: 219px 0 220px;
}

.quotes__list {
  list-style-type: none;
  padding: 0;
  width: 785px;
  margin: 0 auto;
  font-family: "Poppins", "Arial", sans-serif;
  padding-top: 93px;
}

.quotes__wrapper {
  width: 785px;
  margin: 0 auto;
}

.quotes__title {
  color: #D3B47F;
  font-size: 36px;
  line-height: 40px;
  font-weight: 600;
  margin: 0 0 20px;
  position: relative;
}
.quotes__title::before {
  content: "";
  position: absolute;
  top: -92px;
  left: 0;
  right: 0;
  margin: 0 auto;
  background: url("../../img/quotes.svg");
  display: block;
  width: 83px;
  height: 58px;
}

.quotes__text {
  font-size: 16px;
  line-height: 40px;
  text-align: center;
  margin: 0 0 40px;
}

.quotes__author {
  font-size: 24px;
  line-height: 40px;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 6px;
  color: #D3B47F;
}

.quotes .swiper-button-prev {
  left: 0;
}
.quotes .swiper-button-next {
  right: 0;
}

@media (max-width: 1168px) {
  .quotes {
    padding: 50px 0;
  }
  .quotes__wrapper {
    width: auto;
    padding: 0 30px;
  }
  .quotes__title {
    font-size: 30px;
    line-height: 40px;
    text-align: center;
  }
}
.advantages {
  padding: 109px 0 80px;
}

.advantages__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  font-family: "Poppins", "Arial", sans-serif;
}

.advantages__item {
  display: flex;
  gap: 20px;
}
.advantages__item:nth-child(2n) .advantages__wrapper {
  order: 2;
}
.advantages__item:not(:last-child) {
  margin-bottom: 30px;
}

.advantages__wrapper {
  width: 40%;
  padding-top: 178px;
}

.advantages__title {
  text-transform: uppercase;
  font-size: 40px;
  line-height: 88px;
  margin: 0 0 20px;
  position: relative;
}
.advantages__title::after {
  content: "";
  width: 108px;
  height: 2px;
  position: absolute;
  background-color: #D3B47F;
  bottom: -4px;
  left: -10%;
}

.advantages__text {
  color: #808080;
  font-size: 16px;
  line-height: 32px;
}

@media (max-width: 1168px) {
  .advantages {
    padding: 40px 0;
  }
  .advantages__item {
    flex-direction: column;
  }
  .advantages__item:nth-child(2n) .advantages__wrapper {
    order: 0;
  }
  .advantages__wrapper {
    padding-top: 0;
    width: 100%;
  }
  .advantages__img {
    width: 70%;
    height: auto;
  }
}
.photos {
  padding-top: 68px;
  font-family: "Poppins", "Arial", sans-serif;
  color: white;
  background: linear-gradient(#3A4574 60%, white 60% 100%);
}

.photos__title {
  font-size: 48px;
  line-height: 88px;
  margin: 0;
}

.photos__text {
  font-size: 16px;
  line-height: 32px;
  margin: 0 0 50px;
  width: 569px;
}

.photos__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.photos__img {
  display: block;
  margin: 0 auto;
}

.photos {
  position: relative;
}
.photos .swiper-button-prev {
  top: 100px;
  left: auto;
  right: 150px;
}
.photos .swiper-button-next {
  top: 100px;
  left: auto;
  right: 50px;
}

@media (max-width: 1168px) {
  .photos {
    background: #3A4574;
  }
  .photos__img {
    width: 100%;
    height: auto;
  }
}
.contacts {
  padding-top: 101px;
  font-family: "Poppins", "Arial", sans-serif;
}

.contacts__wrapper {
  display: grid;
  grid-template-columns: 40% 60%;
}

.contacts__canvas {
  grid-column: 1/3;
  width: 100%;
  height: 547px;
  background-color: grey;
}

.contacts__title {
  font-size: 48px;
  line-height: 88px;
  margin: 0 0 35px;
}

.contacts__list {
  margin: 0;
}

@media (max-width: 1168px) {
  .contacts {
    padding-top: 40px;
  }
  .contacts__wrapper {
    display: flex;
    flex-direction: column;
  }
}
.contacts-list {
  padding: 0;
  margin: 0;
  color: #2E2E2E;
  list-style-type: none;
  font-weight: 500;
}

.contacts-list__item {
  padding-left: 80px;
  position: relative;
}
.contacts-list__item::before {
  content: "";
  width: 64px;
  height: 64px;
  border-radius: 50%;
  background-color: #D3B47F;
  position: absolute;
  left: 0;
  top: 0;
  background-repeat: no-repeat;
  background-position: center;
}
.contacts-list__item--phone::before {
  background-image: url("../../img/phone.svg");
}
.contacts-list__item--email::before {
  background-image: url("../../img/mail.svg");
}
.contacts-list__item--hours::before {
  background-image: url("../../img/clock.svg");
}
.contacts-list__item--address::before {
  background-image: url("../../img/map.svg");
}

.contacts-list__title {
  font-size: 16px;
  line-height: 32px;
  font-weight: 400;
}

.contacts-list__description {
  margin: 0 0 40px;
  font-size: 24px;
  line-height: 32px;
}

.form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.form__title {
  font-size: 48px;
  line-height: 88px;
  margin: 0 0 29px;
  grid-column: 1/3;
}
.form__title--modal {
  margin: 0;
}

.form__label {
  font-size: 16px;
  line-height: 32px;
}

.form__field {
  display: flex;
  flex-direction: column;
}
.form__field--textarea {
  grid-column: 1/3;
}

.form__input {
  padding: 28px 16px;
  border: 1px solid #CCCCCC;
  font-family: "Poppins", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
}
.form__input--textarea {
  min-height: 125px;
}

.form__button {
  width: 270px;
  height: 100px;
  margin-bottom: 50px;
}
.form__button--modal {
  margin: 0 auto;
  grid-column: 1/3;
}

@media (max-width: 1168px) {
  .form {
    display: flex;
    flex-direction: column;
  }
  .form__button {
    align-self: center;
  }
}
.footer {
  padding: 40px 0 526px;
}

.footer__nav {
  display: flex;
  justify-content: space-between;
}

.footer__socials a {
  color: black;
  border-color: black;
}

@media (max-width: 1168px) {
  .footer__nav {
    flex-direction: column;
    align-items: center;
  }
  .footer__logo {
    margin-bottom: 20px;
  }
}
.swiper .swiper-button-prev,
.swiper .swiper-button-next {
  color: white;
}
.swiper .swiper-button-prev:hover,
.swiper .swiper-button-next:hover {
  color: #D3B47F;
}

.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 700px;
  height: 600px;
  margin: auto;
  background-color: rgba(255, 255, 255, 0.9);
  display: flex;
}

.modal--close {
  display: none;
}

.modal--open {
  display: flex;
}

.modal__close {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  border: none;
  background-color: transparent;
}

.modal__content {
  position: relative;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  z-index: 50;
  width: 100%;
  border: 1px solid black;
  background-color: white;
  box-shadow: 0 5px 10px 0 rgba(0, 1, 1, 0.25);
  padding: 30px;
}

.modal__overlay {
  opacity: 0;
  visibility: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 20;
  transition: 0.3s all;
}

.modal--open .modal__overlay {
  opacity: 1;
  visibility: visible;
}

.modal__button-close {
  position: absolute;
  top: 50px;
  right: 30px;
  width: 50px;
  height: 50px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.modal__button-close::before {
  content: "";
  width: 35px;
  height: 3px;
  transform: rotate(45deg);
  background-color: black;
  position: absolute;
  top: 20px;
  left: 10px;
}
.modal__button-close::after {
  content: "";
  width: 35px;
  height: 3px;
  transform: rotate(-45deg);
  background-color: black;
  position: absolute;
  top: 20px;
  right: 5px;
}
.modal__button-close:hover::before, .modal__button-close:hover::after {
  background-color: rgba(0, 0, 0, 0.3);
}

@media (max-width: 1168px) {
  .modal__content {
    width: auto;
  }
}